.main-page__wrapper {
  width: 320px;
  margin: 0 auto;
}

.main-page__title {
  padding: 40px 25px 0 25px;
  margin: 0;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;
  width: 270px;
}

.main-page__description {
  padding: 0 25px;
  margin: 27px 0 5px 0;
  font-size: 14px;
  line-height: 26px;
  font-weight: 400;
  text-align: center;
  width: 270px;
  color: #333333;
}

@media (min-width: @tablet-width) {
  .main-page__wrapper {
    width: @tablet-width;
  }
  .main-page__title {
    font-size: 21px;
    padding-top: 50px;
    margin: 0 auto;
    width: 450px;
  }
  .main-page__description {
    padding-top: 2px;
    padding-bottom: 45px;
    width: auto;
  }
}

@media (min-width: @desktop-width) {
  .main-page__wrapper {
    width: @desktop-width;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
    background-color: #ffffff;
  }
}
